import { Component, OnInit } from '@angular/core';
import { ValidateService } from 'src/app/services/validate.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { AuthService } from 'src/app/services/auth.service';
import { Router } from '@angular/router';
@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})
export class RegisterComponent implements OnInit {
  name: String;
  username: String;
  email: String;
  password: String; 
  constructor( 
              private validateService : ValidateService,
              private flashMessage: FlashMessagesService, 
              private authService: AuthService, 
              private routerService: Router ) { }
  ngOnInit() {
  }

  onRegisterSubmit(){
    const user = {
      name: this.name,
      username: this.username,
      email: this.email,
      password: this.password
    };

    // Required fields
    if( !this.validateService.validateRegister(user)) {
      this.flashMessage.show( 'Please fill in all fields.', {cssClass: 'alert-danger', timeout: 3000});
      return false;
    }
    if( !this.validateService.validateEmail(user.email)) {
      this.flashMessage.show( 'Please validate email.', {cssClass: 'alert-danger', timeout: 3000});
      return false;
    }
    
    // Register User
    this.authService.registerUser(user).subscribe({
      next: data => {
        this.flashMessage.show( 'You\'re now registered and can now login.', {cssClass: 'alert-success', timeout: 3000});
        this.routerService.navigate(['/login']);
      }, error: err => {
        this.flashMessage.show( 'Error: '+err, {cssClass: 'alert-danger', timeout: 3000});
        this.routerService.navigate(['/register']);
      }
    });
  }

}
